.button {
  background-color: #000000;
  border: 1px solid white;
  color: white;
  width: 150px;
  height: 50px;
  font-size: 30px;
  display: block;
  cursor: pointer;
}

.button:focus {
  outline-color: greenyellow;
}

.button:disabled {
  background-color: transparent;
  color: rgba(0, 0, 0, .5);
}